import React from 'react';

interface EmailProps {
    name: string;
    email: string;
    message: string;
}



export const Email: React.FC<EmailProps> = ({ name, email, message }) => {

    return (
        <div>
            <h1>Email from {name}</h1>
            <p>{message}</p>
            <p>Contact: {email}</p>
        </div>
    );
};


export default Email;
