import React, { useState } from 'react';
import { InputText } from 'primereact/inputtext';
import { InputTextarea } from 'primereact/inputtextarea';
import { Button } from 'primereact/button';
import { Card } from 'primereact/card';
import { Toast } from 'primereact/toast';
import ReCAPTCHA from "react-google-recaptcha";

import { Resend } from 'resend';
import { Email } from '../components/Email';
// import MyGlobe from '../components/MyGlobe';

import './Contact.css';

const resend = new Resend('re_Tf3cBeb1_fEFhNNCjBKpFxd4mYDm3uT7b');

const Contact: React.FC = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [captchaValid, setCaptchaValid] = useState(false);

  const toast = React.useRef<Toast>(
    null
  );

  const recaptcha = React.useRef<ReCAPTCHA>(
    null
  );

  const handleSubmit = async (e: React.FormEvent) => {

    e.preventDefault();
    if (!captchaValid) {
      alert("Please complete the captcha.");
      return;
    }

    if (!recaptcha.current) {
      return;
    }

    // Send email with resend
    try {
      const response = await resend.emails.send({
        from: email,
        to: 'consulting@orbital-pass.com',
        subject: 'Form Submission',
        react: <Email name={name} email={email} message={message}/>,
      });
      const data = response.data;
      console.log(data);
      if (response.data) {
        toast.current?.show({ severity: 'success', summary: 'Success', detail: 'Your message has been sent!' });
        // Reset the form
        setName('');
        setEmail('');
        setMessage('');
        setCaptchaValid(false);
        recaptcha.current?.reset();
      } else {
        toast.current?.show({ severity: 'error', summary: 'Error', detail: 'Your message has not been sent.' });
      }
    } catch (error) {
      toast.current?.show({ severity: 'error', summary: 'Error', detail: 'There was an error sending your message. Please try again later.' });
    }
  };

  const handleCaptchaResponse = (token: string | null) => {
    if (token) {
      setCaptchaValid(true);
    } else {
      setCaptchaValid(false);
    }
  };

  return (
    <div className="contact-page">
      <Toast ref={toast} />

      {/* Contact Info */}

      <div className="contact-info" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        {/* <img src="/assets/profile.jpg" alt="Profile" className="profile-image" /> */}
        <div className="profile-description">
          <h1>Contact</h1>
          <p>
            I am a team player, problem solver, and passionate about delivering innovative solutions to your projects.
            <br />
            Connect with me on social media, fill out the form below or send an email to consulting at orbital-pass dot com to get in touch.
          </p>
        </div>

        <div className="social-buttons">
          <a href="https://www.linkedin.com/in/tim-van-der-lee-33247b116/">
            <Button icon="pi pi-linkedin" className="p-button-rounded p-button-outlined" aria-label="LinkedIn" />
          </a>
          <a href="https://github.com/Tim024">
            <Button icon="pi pi-github" className="p-button-rounded p-button-outlined" aria-label="Github" />
          </a>
        </div>
      </div>

      {/* Contact Form in a Card */}
      <div className="contact-form-container">
        <Card className="contact-form-card">
          <form onSubmit={handleSubmit}>
            <div className="p-field">
              <label htmlFor="name">Name</label>
              <InputText id="name" value={name} onChange={(e) => setName(e.target.value)} maxLength={50} required />
            </div>
            <div className="p-field">
              <label htmlFor="email">Email</label>
              <InputText id="email" value={email} onChange={(e) => setEmail(e.target.value)} maxLength={100} required />
            </div>
            <div className="p-field">
              <label htmlFor="message">Message</label>
              <InputTextarea id="message" value={message} onChange={(e) => setMessage(e.target.value)} rows={1} autoResize required />
            </div>


            <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '2rem' }}>
              
              <ReCAPTCHA
                sitekey="6Lfyi1UqAAAAAHTJHA-ffIlq60Jk05NAbgk6RALi"  // Replace this with your actual site key from Google reCAPTCHA
                onChange={handleCaptchaResponse}
                ref={recaptcha}
              // size="invisible"
              // theme="dark"
              // style={{ display: 'inline-block'}}
              />
              <Button label="Submit" type="submit" className="p-button-rounded p-button-info" disabled={!captchaValid} style={{ height: "50px", alignSelf: "flex-end" }} />
            </div>
          </form>
        </Card>
      </div>

      {/* Globe with Satellite */}
      {/* <MyGlobe /> */}
    </div>
  );
};

export default Contact;
